@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;200;300;400;500;600;700;800;900&family=Oswald:wght@200;300;400;500;600;700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: all 0.2s;
}

html {
  font-size: 15px;
}

ul, li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  font-family: inherit;
}

img {
  display: block;
  max-width: 100%;
}

figure {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

body {
  font-family: "Noto Sans KR", sans-serif;
  background-color: #eeeae3;
  color: #373633;
}

.Topbtn {
  display: block;
  position: fixed;
  bottom: 15vh;
  right: 10vw;
  width: 40px;
  color: #373633;
  height: 40px;
  border-radius: 100%;
  background-color: #eeeae3;
  border: 1px solid #373633;
  cursor: pointer;
  transition: 0.3s;
  z-index: 999;
}

.Topbtn:hover {
  background-color: #373633;
  color: #eeeae3;
}

header {
  width: 100%;
  height: 7vh;
  position: fixed;
  background-color: #eeeae3;
  top: 0;
  left: 0;
  z-index: 9999;
  overflow: hidden;
}
header .headerI {
  max-width: 1600px;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .headerI ul {
  display: flex;
}
header .headerI ul li {
  font-weight: 400;
  font-size: 1.2rem;
  margin: 0 1.5vw;
  font-weight: 500;
}
header .headerI h1 {
  font-size: 3rem;
}
header .headerI h1 a.tt {
  font-family: "Oswald", sans-serif;
}

#wrap {
  margin-top: 15vh;
}

.headerF {
  background-color: #373633;
  color: #eeeae3;
}

.mainText {
  max-width: 1600px;
  min-height: 30vh;
  margin: auto;
  margin-bottom: 15vh;
  padding: 5vh 0;
  overflow: hidden;
}
.mainText p {
  min-height: 30vh;
  font-size: 2rem;
  line-height: 190%;
  font-weight: 500;
  overflow: hidden;
}

.mainImg {
  width: 100%;
  overflow: hidden;
  margin-bottom: 7vh;
}
.mainImg figure {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.mainImg img {
  width: 100%;
}

#About {
  max-width: 1600px;
  margin: auto;
  margin-bottom: 15vh;
  display: flex;
  justify-content: space-between;
}
#About h2 {
  font-size: 3rem;
}
#About .AboutText {
  flex-basis: 70%;
}
#About .AboutText p {
  line-height: 160%;
  margin-bottom: 2rem;
}

#Art {
  max-width: 1600px;
  overflow: hidden;
  margin: auto;
  margin-bottom: 15vh;
}
#Art h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
  font-family: "Oswald", sans-serif;
}
#Art p {
  margin-bottom: 2rem;
  line-height: 160%;
}
#Art p span {
  display: block;
}
#Art .ArtImg {
  margin-bottom: 15vh;
}
#Art .artLife h2 {
  text-align: center;
  margin-bottom: 5vh;
}
#Art .artBox {
  width: 80%;
  margin: auto;
  margin-bottom: 10vh;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 3vw;
}
#Art .artBox .b1 {
  grid-column: 2/3;
  grid-row: 2/3;
}
#Art .artBox .b2 {
  grid-column: 3/5;
  grid-row: 1/3;
  position: relative;
  width: 100%;
  height: 100%;
  position: relative;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1.5s;
}
#Art .artBox .b2 .front {
  position: absolute;
  transform: rotateY(0deg);
  transition: all 1.5s;
  filter: grayscale(1);
}
#Art .artBox .b2 .back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  color: #fff;
  font-size: 6vw;
  opacity: 0;
  transform: rotateY(-180deg);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1.5s;
}
#Art .artBox .b3 {
  grid-column: 1/3;
  grid-row: 3/5;
  width: 100%;
  height: 100%;
  position: relative;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1.5s;
}
#Art .artBox .b3 .front {
  position: absolute;
  transform: rotateY(0deg);
  transition: all 1.5s;
  filter: grayscale(1);
}
#Art .artBox .b3 .back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  color: #fff;
  font-size: 6vw;
  opacity: 0;
  transform: rotateY(-180deg);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1.5s;
}
#Art .artBox .b3:hover .front {
  transform: rotateY(180deg);
  filter: grayscale(0);
}
#Art .artBox .b3:hover .back {
  transform: rotateY(0deg);
  opacity: 1;
}
#Art .artBox .b2:hover .front {
  transform: rotateY(180deg);
  filter: grayscale(0);
}
#Art .artBox .b2:hover .back {
  transform: rotateY(0deg);
  opacity: 1;
}
#Art .artBox .b4 {
  grid-column: 3/4;
  grid-row: 3/4;
}
#Art .artBox figure {
  width: 100%;
  height: 100%;
}

#Product {
  margin: auto;
  margin-bottom: 15vh;
  display: flex;
  flex-direction: column;
}
#Product .Artist {
  width: 100%;
  height: 100%;
  max-width: calc(100% - (100% - 1600px) / 2);
  margin-bottom: 5vh;
  align-self: flex-end;
  display: flex;
  justify-content: space-between;
}
#Product .Artist .artist_left {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
#Product .Artist .artist_left h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
}
#Product .Artist .artist_left p {
  font-size: 1.1rem;
}
#Product .Artist .artist_left button {
  display: block;
  width: 250px;
  height: 75px;
  align-self: center;
  border: 1px solid #373633;
  background-color: transparent;
  font-size: 1.8rem;
  font-weight: 400;
  cursor: pointer;
  position: relative;
  color: #373633;
  transition: 0.3s;
}
#Product .Artist .artist_left button::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #373633;
  z-index: -1;
  transition: 0.5s linear;
  opacity: 0;
}
#Product .Artist .artist_left button:hover {
  color: #fff;
}
#Product .Artist .artist_left button:hover::after {
  opacity: 1;
}
#Product .Artist .artist_right {
  width: 50%;
}
#Product .Product {
  width: 100%;
  max-width: calc(100% - (100% - 1600px) / 2);
  display: flex;
  align-self: flex-start;
  justify-content: space-between;
}
#Product .Product .product_right {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
#Product .Product .product_right h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
}
#Product .Product .product_right p {
  font-size: 1.1rem;
}
#Product .Product .product_right button {
  display: block;
  width: 250px;
  height: 75px;
  align-self: center;
  border: 1px solid #373633;
  color: #373633;
  background-color: transparent;
  font-size: 1.8rem;
  font-weight: 400;
  cursor: pointer;
  transition: 0.3s;
  position: relative;
}
#Product .Product .product_right button::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #373633;
  z-index: -1;
  transition: 0.5s linear;
  opacity: 0;
}
#Product .Product .product_right button:hover {
  color: #fff;
}
#Product .Product .product_right button:hover::after {
  opacity: 1;
}
#Product .Product .product_left {
  width: 50%;
}

#popup {
  margin-bottom: 15vh;
}
#popup h2 {
  max-width: 1600px;
  margin: auto;
  font-size: 3rem;
  margin-bottom: 4rem;
}
#popup .swiper {
  margin-left: calc((100% - 1600px) / 2);
}
#popup .swiper figure {
  margin-bottom: 1rem;
}
#popup .swiper p {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
#popup .swiper span {
  font-size: 0.7rem;
  color: #808080;
}

.mySwiper {
  margin-left: 0;
}

#videos {
  max-width: 1600px;
  margin: auto;
  margin-bottom: 25vh;
}
#videos h2 {
  max-width: 1600px;
  margin: auto;
  font-size: 3rem;
  margin-bottom: 4rem;
}
#videos .video_content {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 6vw 3vw;
}
#videos .video_content .vbox {
  position: relative;
}
#videos .video_content .vbox h3 {
  margin: 0.5rem 0;
}
#videos .video_content .vbox p {
  color: #808080;
  font-size: 0.8rem;
}
#videos .video_content .vbox figure {
  position: relative;
}
#videos .video_content .vbox figure span {
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  height: 100%;
  top: 0;
  left: 0;
}
#videos .video_content .vbox figure:hover span {
  opacity: 0;
}

footer {
  border-top: 1px solid #373633;
  padding: 2rem 0;
}
footer .f_inner {
  max-width: 1600px;
  margin: auto;
  height: 50vh;
  display: flex;
