#Sabout {
  max-width: 1600px;
  margin: auto;
  margin-bottom: 25vh;
  display: flex;
  justify-content: space-between;
}
#Sabout .fix {
  flex-basis: 10%;
}
#Sabout .fix h2 {
  position: sticky;
  top: 15vh;
  left: 0;
}
#Sabout .contents {
  flex-basis: 80%;
}
#Sabout .contents .con1 {
  margin-bottom: 20vh;
  display: flex;
  flex-direction: column;
}
#Sabout .contents .con1 figure {
  width: 100%;
  align-self: flex-end;
  margin-bottom: 10vh;
}
#Sabout .contents .con1 figure img {
  width: 100%;
}
#Sabout .contents .con1 p {
  line-height: 170%;
  margin-bottom: 2.5rem;
}
#Sabout .contents .con1 p:nth-child(2n+1) {
  margin-bottom: 0.5rem;
}
#Sabout .contents .con2 h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
  font-family: "Oswald", sans-serif;
}
#Sabout .contents .con2 p {
  margin-bottom: 2rem;
  line-height: 170%;
}
#Sabout .contents .con2 p span {
  display: block;
}
#Sabout .contents .con2 figure {
  width: 100%;
}
#Sabout .contents .con2 figure img {
  width: 100%;
}

@media screen and (max-width: 1600px) {
  #Sabout {
    max-width: 1200px;
  }
}
@media screen and (max-width: 1240px) {
  #Sabout {
    max-width: 1024px;
  }
}
@media screen and (max-width: 1024px) {
  #Sabout {
    max-width: 767px;
  }
}
@media screen and (max-width: 850px) {
  #Sabout {
    width: 95%;
    max-width: auto;
  }
  #Sabout .fix {
    flex-basis: 15%;
  }
  #Sabout .fix h2 {
    text-align: center;
  }
}
@media screen and (max-width: 450px) {
  #Sabout {
    width: 95%;
    max-width: auto;
  }
}/*# sourceMappingURL=About.css.map */