#subworks {
  max-width: 1600px;
  margin: auto;
  margin-bottom: 20vh;
}
#subworks .workbox {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 3vw 6vw;
}
#subworks .workbox figure {
  width: 100%;
  margin-bottom: 1.5rem;
}
#subworks .workbox h3 {
  margin-bottom: 1rem;
}
#subworks .workbox p {
  color: #808080;
  font-size: 0.8rem;
}

@media screen and (max-width: 1600px) {
  #subworks {
    max-width: 1200px;
  }
}
@media screen and (max-width: 1240px) {
  #subworks {
    max-width: 1024px;
  }
}
@media screen and (max-width: 1024px) {
  #subworks {
    max-width: 767px;
  }
  #subworks .workbox {
    grid-template-columns: repeat(3, 1fr);
    gap: 3vw 6vw;
  }
}
@media screen and (max-width: 850px) {
  #subworks {
    width: 95%;
    max-width: auto;
  }
  #subworks .workbox {
    grid-template-columns: repeat(2, 1fr);
    gap: 8vh 3vw;
  }
}
@media screen and (max-width: 450px) {
  #subworks .workbox {
    grid-template-columns: repeat(2, 1fr);
    gap: 7vh 4vw;
  }
}/*# sourceMappingURL=Works.css.map */