#subworks{
  max-width: 1600px;
  margin: auto;
  margin-bottom: 20vh;
  .workbox{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3vw 6vw;
    figure{
      width: 100%;
      margin-bottom: 1.5rem;
    }
    h3{
      margin-bottom: 1rem;
    }
    p{
      color: #808080;
      font-size: .8rem;
    }
  }
}

@media screen and (max-width:1600px) {
  #subworks{
    max-width: 1200px;
  }
}

@media screen and (max-width:1240px) {
  #subworks{
    max-width: 1024px;
  }
}

@media screen and (max-width:1024px) {
  #subworks{
    max-width: 767px;
    .workbox{
      grid-template-columns: repeat(3,1fr);
      gap: 3vw 6vw  
    }
  }
}

@media screen and (max-width:850px) {
  #subworks{
    width: 95%;
    max-width: auto;
    .workbox{
      grid-template-columns: repeat(2,1fr);
      gap: 8vh 3vw;  
    }
  }
}

@media screen and (max-width:450px) {
  #subworks{
    .workbox{
      grid-template-columns: repeat(2,1fr);
      gap: 7vh 4vw;  
    }
  }
}
