#Sabout{
  max-width: 1600px;
  margin: auto;
  margin-bottom: 25vh;
  display: flex;
  justify-content: space-between;
  .fix{
    flex-basis: 10%;
    h2{
      position: sticky;
      top: 15vh;
      left: 0;
    }
  }
  .contents{
    flex-basis: 80%;
    .con1{
      margin-bottom: 20vh;
      display: flex;
      flex-direction: column;
      figure{
        width: 100%;
        align-self: flex-end;
        margin-bottom: 10vh;
        img{
          width: 100%;
        }
      }
      p{
        line-height: 170%;
        margin-bottom: 2.5rem;
      }
      p:nth-child(2n+1){
        margin-bottom: .5rem;
      }
    }
    .con2{
      h2{
        font-size: 3rem;
        margin-bottom: 2rem;
        font-family: 'Oswald', sans-serif;
      }
      p{
        margin-bottom: 2rem;
        line-height: 170%;
        span{
          display: block  ;
        }
      }
      figure{
        width: 100%;
        img{
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width:1600px) {
  #Sabout{
    max-width: 1200px;
  }
}

@media screen and (max-width:1240px) {
  #Sabout{
    max-width: 1024px;
  }
}

@media screen and (max-width:1024px) {
  #Sabout{
    max-width: 767px;
  }
}

@media screen and (max-width:850px) {
  #Sabout{
    width: 95%;
    max-width: auto;
    .fix{
      flex-basis: 15%;
      h2{
        text-align: center;
      }
    }
  }
}


@media screen and (max-width:450px) {
  #Sabout{
    width: 95%;
    max-width: auto;
  }
}